import {Component, OnDestroy, OnInit} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';
import {MatDialog} from '@angular/material';

import {ErrorDialogComponent} from 'app/shared/error-dialog/error-dialog.component';
import {InfoMessage} from 'app/core/objects/info-message';
import {ErrorService} from 'app/core/services/error.service';
import {RouterService} from 'app/core/services/router.service';
import {LoadingService} from 'app/core/services/loading.service';
import {SystemService} from 'app/core/services/system.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {
  loadingShow: boolean;

  private alertObservable: Observable<InfoMessage>;
  private alertSubscription: Subscription;

  private loadingObservable: Observable<boolean>;
  private loadingSubscription: Subscription;

  private infoMessage: InfoMessage; // 弹窗的传入数据


  constructor(private dialog: MatDialog,
              private errorService: ErrorService,
              private routerService: RouterService,
              private loadingService: LoadingService,
              private systemService: SystemService) {
  }

  ngOnInit() {
    this.infoStart();
    this.loadingStart();
  }

  ngOnDestroy() {
    this.infoStop();
    this.loadingStop();
  }

  private infoStart(): void {
    this.alertObservable = this.errorService.getError();
    this.alertSubscription = this.alertObservable.subscribe(
      (value: InfoMessage) => {
        this.popAlertView(value);
        console.log('-- update error! --', value)
      },
      error => {
        console.log('-- error! --')
      },
      () => {
        console.log('-- end! --')
      }
    );
  }

  private infoStop(): void {
    if (this.alertSubscription) {
      this.alertSubscription.unsubscribe();
    }
  }

  // 弹出警告框
  private popAlertView(infoMessage: InfoMessage): void {

    this.infoMessage = infoMessage;

    const dialogRef = this.dialog.open(ErrorDialogComponent, {
      width: '8.3333rem',
      data: {
        errorInfo: infoMessage.data,
        autoCloseTime: infoMessage.autoCloseTime
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (!this.infoMessage.noRouter) {
        if (this.infoMessage.routerInfo) {
          // 跳到指定位置
          this.routerService.goTo(this.infoMessage.routerInfo.commands, this.infoMessage.routerInfo.extras);
        } else {
          // 后退到主菜单界面，并清除用户信息
          this.systemService.backMainMenu();
        }
      }
    });

  }

  private loadingStart(): void {
    this.loadingObservable = this.loadingService.getLoading();
    this.loadingSubscription = this.loadingObservable.subscribe(
      (value: boolean) => {
        this.loadingShow = value;
        console.log('-- update loading! --', value)
      },
      error => {
        console.log('-- error! --')
      },
      () => {
        console.log('-- end! --')
      }
    );
  }

  private loadingStop(): void {
    if (this.loadingSubscription) {
      this.loadingSubscription.unsubscribe();
    }
  }

}



