// 设备身份认证

import {Injectable} from '@angular/core';
import {Subscription} from 'rxjs/Rx';

import {BaseService} from 'app/core/bases/base-service';
import {ShellRequestService} from 'app/protocol/shell/shell-request.service';
import {RouterService} from 'app/core/services/router.service';
import {LogService} from 'app/core/services/log.service';
import {ServerRequestService} from 'app/protocol/server/server-request.service';
import {ServerApiLibrary} from 'app/protocol/server/server-api-library';
import {InputDeviceLogin} from 'app/protocol/server/input-device-login';
import {ResponseDataWrap} from 'app/core/objects/response-data-wrap';
import {OutputGetConfig} from 'app/protocol/shell/output-get-config';
import {PathLibrary} from 'app/core/libs/path-library';
import {DeviceParameter} from 'app/core/public/device-parameter';
import {HttpService} from 'app/core/public/http.service';

@Injectable()
export class AuthService extends BaseService {

  private web2ShellGetConfigSubscription: Subscription;

  constructor(private shellRequestService: ShellRequestService,
              private serverRequestService: ServerRequestService,
              private routerService: RouterService,
              private httpService: HttpService) {
    super();
  }

  init(): void {
  }


  // 开始设备身份认证
  startAuth(): void {
    LogService.debug('get config info start ...');
    this.web2ShellGetConfigSubscription = this.shellRequestService.web2ShellGetConfig().subscribe(
      data => this.getConfigSuc(data),
      error => this.getConfigErr(error)
    );
  }

  // 获取设备配置，成功
  private getConfigSuc(outputGetConfig: OutputGetConfig) {

    this.web2ShellGetConfigSubscription.unsubscribe();

    if (outputGetConfig.hospitalID && outputGetConfig.server && outputGetConfig.mode) {
      // save config
      ServerApiLibrary.authInfo.serverURL = outputGetConfig.server;
      ServerApiLibrary.authInfo.hospitalId = outputGetConfig.hospitalID;
      ServerApiLibrary.authInfo.deviceID = outputGetConfig.deviceID;
      ServerApiLibrary.authInfo.macAddress = outputGetConfig.macAddress;
      ServerApiLibrary.authInfo.download = outputGetConfig.download;
      ServerApiLibrary.authInfo.webVersion = outputGetConfig.webVersion;
      ServerApiLibrary.authInfo.mode = outputGetConfig.mode;

      console.log('----- 001 ------', outputGetConfig.download);
      console.log('----- 002 ------', ServerApiLibrary.authInfo.download);

      DeviceParameter.setMode(outputGetConfig.mode);

      this.getTokenFromServer(outputGetConfig);

    } else {
      // 无法获取相关信息时，跳转错误页面

      // log
      LogService.error('get config fail: [parameters invalid]. Show Error Page.');

      // go to error page
      this.routerService.goTo([PathLibrary.errorPage]);
    }
  }

  // 获取设备配置，失败
  private getConfigErr(errorMsg: string) {
    // log
    LogService.error('get config fail: [' + errorMsg + ']. Show Error Page.');

    // go to error page
    this.routerService.goTo([PathLibrary.errorPage]);
  }

  // 去服务器进行设备身份认证
  private getTokenFromServer(outputGetConfig: OutputGetConfig) {
    const inputDeviceLogin: InputDeviceLogin = new InputDeviceLogin();
    inputDeviceLogin.hospital_id = outputGetConfig.hospitalID;
    inputDeviceLogin.device_id = outputGetConfig.deviceID;
    inputDeviceLogin.mac_address = outputGetConfig.macAddress;

    LogService.debug('get token start ...');
    this.serverRequestService.deviceLogin(inputDeviceLogin)
      .subscribe(
        data => this.getTokenFromServerSuc(data, true),
        error => this.getTokenFromServerErr(error)
      );
  }

  /**
   * 获取 token 成功的处理
   * @param {ResponseDataWrap} responseDataWrap 服务器返回的数据
   * @param {boolean} goToUI 是否自动跳转到指定UI（主菜单）
   */
  private getTokenFromServerSuc(responseDataWrap: ResponseDataWrap, goToUI: boolean): void {
    // update token
    ServerApiLibrary.authInfo.token = responseDataWrap.responseToken;

    LogService.debug('get token suc, value:' + responseDataWrap.responseToken);

    if (goToUI) {
      // 跳转UI，进入指定UI
      this.routerService.goTo([PathLibrary.mainMenu]);
    }
  }

  // 获取 token 失败的处理
  private getTokenFromServerErr(errorMsg: string): void {
    // log
    LogService.error('get token fail: [' + errorMsg + ']');

    // 跳转UI，进入出错等待界面
    this.routerService.goTo([PathLibrary.errorWait]);
  }

  // ---------------------------------

  // 去服务器进行设备身份认证
  public renewTokenFromServer() {
    const inputDeviceLogin: InputDeviceLogin = new InputDeviceLogin();
    inputDeviceLogin.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputDeviceLogin.device_id = ServerApiLibrary.authInfo.deviceID;
    inputDeviceLogin.mac_address = ServerApiLibrary.authInfo.macAddress;

    LogService.debug('get token start ...');
    this.serverRequestService.deviceLogin(inputDeviceLogin)
      .subscribe(
        data => this.getTokenFromServerSuc(data, false),
        error => this.getTokenFromServerErr(error)
      );
  }

  // ---------------------------------

  // 去服务器进行web版本校验
  public checkVersionFromServer() {
    this.httpService
      .requestRemoteJson(ServerApiLibrary.authInfo.download)
      .subscribe(
        data => this.checkVersionFromServerSuc(data, false),
        error => this.checkVersionFromServerErr(error)
      );
  }

  private checkVersionFromServerSuc(responseDataWrap: ResponseDataWrap, goToWelcomeUI: boolean): void {
    console.log('----- 555 ------');
    // // update token
    // ServerApiLibrary.authInfo.token = responseDataWrap.responseToken;
    //
    // LogService.debug('get token suc, value:' + responseDataWrap.responseToken);
    //
    // if (goToWelcomeUI) {
    //   // 跳转UI，进入欢迎UI
    //   this.routerService.goTo([PathLibrary.banner]);
    // }
  }

  private checkVersionFromServerErr(errorMsg: string): void {
    console.log('----- 666 ------', errorMsg);
    // // log
    // LogService.error('get token fail: [' + errorMsg + ']');
    //
    // // 跳转UI，进入出错等待界面
    // this.routerService.goTo([PathLibrary.errorWait]);
  }

}



