// 设备登陆信息，将会持续保存

export class AuthInfo {
  serverURL: string;
  hospitalId: string;
  deviceID: string; // 设备ID
  macAddress: string; // 设备mac地址
  mode: string; // 运行模式
  download: string; // 版本更新地址
  webVersion: string; // web的当前版本号
  token: string;
}



