// 刷卡页面
import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs/Rx';
import {ActivatedRoute} from '@angular/router';

import {PathLibrary} from 'app/core/libs/path-library';
import {LogService} from 'app/core/services/log.service';
import {ErrorService} from 'app/core/services/error.service';
import {SystemService} from 'app/core/services/system.service';
import {LoadingService} from 'app/core/services/loading.service';
import {ShellRequestService} from 'app/protocol/shell/shell-request.service';
import {ShellApiLibrary} from 'app/protocol/shell/shell-api-library';
import {OutputReadMagneticCard} from 'app/protocol/shell/output-read-magnetic-card';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {UserService} from 'app/core/services/user.service';
import {DeviceParameter} from 'app/core/public/device-parameter';
import {InfoMessage} from 'app/core/objects/info-message';
import {InfoLibrary} from 'app/core/libs/info-library';

@Component({
  selector: 'app-read-card',
  templateUrl: './read-card.component.html',
  styleUrls: ['./read-card.component.css']
})
export class ReadCardComponent implements OnInit, OnDestroy {

  showReadCardButton = DeviceParameter.isReadCardDebugMode; // 模拟读卡器时使用
  outpatientInfoShow = false; // 用户信息提示，为了解决读卡器问题
  tempCardID: string; // 当前卡号
  nextPath: string; // 下一个页面

  private subscriptionGetParameter: Subscription; // 从服务器获取用户信息
  private web2ShellGetReadCardResultSubscription: Subscription; // 从设备获取卡信息

  constructor(private activatedRoute: ActivatedRoute,
              private shellRequestService: ShellRequestService,
              private userService: UserService,
              private errorService: ErrorService,
              private systemService: SystemService,
              private loadingService: LoadingService) {
  }

  ngOnInit() {
    this.getParameterFromRoute();
    this.waitReadCardResult();
  }

  ngOnDestroy() {
    if (this.subscriptionGetParameter) {
      this.subscriptionGetParameter.unsubscribe();
    }
    if (this.web2ShellGetReadCardResultSubscription) {
      this.web2ShellGetReadCardResultSubscription.unsubscribe();
    }
  }

  // 触发模拟读卡器的功能
  testReadCard() {
    this.shellRequestService.shell2WebTest(ShellApiLibrary.readMagneticCard);
  }

  // 解析路由参数，获取下一步要去的位置
  private getParameterFromRoute() {
    this.subscriptionGetParameter = this.activatedRoute.params.subscribe(params => {
      this.nextPath = params[PathLibrary.readCardParameter];
      LogService.debug('nextPath: ' + this.nextPath);
    });
  }

  // 等待读卡结果
  private waitReadCardResult() {
    this.web2ShellGetReadCardResultSubscription = this.shellRequestService.web2ShellReadMagneticCard()
      .subscribe(
        data => this.getReadCardResultSuc(data),
        error => this.getReadCardResultErr(error)
      );
  }

  // 等待读卡结果，成功
  private getReadCardResultSuc(outputReadMagneticCard: OutputReadMagneticCard) {
    LogService.debug('get read card result suc: [' + outputReadMagneticCard.cardID + ']');
    this.tempCardID = outputReadMagneticCard.cardID;

    this.getPatientInfo(this.tempCardID);
    this.web2ShellGetReadCardResultSubscription.unsubscribe();
  }

  // 等待读卡结果，失败
  private getReadCardResultErr(errorMsg: string) {
    LogService.error('get read card result fail: [' + errorMsg + ']');
    this.systemService.backMainMenu();
  }

  // 从服务器获取用户信息
  private getPatientInfo(inpatientNum: string): void {
    this.loadingService.showLoading();

    this.userService.getOutPatientInfoFromServer(inpatientNum).subscribe(
      data => this.getPatientInfoSuc(data),
      error => this.getPatientInfoErr(error)
    );
  }

  // 从服务器获取用户信息，成功
  private getPatientInfoSuc(outputQueryOutPatientInfo: OutputQueryOutPatientInfo): void {
    this.loadingService.hiddenLoading();
    LogService.debug('getPatientInfoSuc: ' + outputQueryOutPatientInfo);
    this.outpatientInfoShow = true;
  }

  // 从服务器获取用户信息，失败
  private getPatientInfoErr(errorMsg: string): void {
    LogService.error('getPatientInfoErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(InfoLibrary.outpatientNumError);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }

}



