// shell 接口访问，由工具生成

import {Injectable} from '@angular/core';
import {Observable, Subscriber} from 'rxjs/Rx';

import {ShellUtility} from 'app/core/public/shell-utility';
import {ShellApiLibrary} from 'app/protocol/shell/shell-api-library';
import {BaseShellService} from 'app/protocol/shell/base-shell-service';
import {MockShell} from 'app/core/mock/mock-shell';
import {DeviceParameter} from 'app/core/public/device-parameter';

import {InfoMessage} from 'app/core/objects/info-message';
import {InputSetTimeForPowerOff} from 'app/protocol/shell/input-set-time-for-power-off';
import {InputSetTimeForPowerOn} from 'app/protocol/shell/input-set-time-for-power-on';
import {InputLog} from 'app/protocol/shell/input-log';
import {OutputGetConfig} from 'app/protocol/shell/output-get-config';
import {OutputReadMagneticCard} from 'app/protocol/shell/output-read-magnetic-card';
import {OutputReadICCard} from 'app/protocol/shell/output-read-i-c-card';
import {OutputReadIDCard} from 'app/protocol/shell/output-read-i-d-card';
import {OutputReadHealthCard} from 'app/protocol/shell/output-read-health-card';

import 'assets/external/external.js';

declare function requestServiceInJS(data: string, isDebugMode: boolean);  // 对shell接口函数，为了便于TS中代码编译检查

@Injectable()
export class ShellRequestService implements BaseShellService {

  private static instance: ShellRequestService;

  private logObservable: Observable<InfoMessage>;
  private logSubscriber: Subscriber<InfoMessage>;

  private getConfigObservable: Observable<OutputGetConfig>;
  private getConfigSubscriber: Subscriber<OutputGetConfig>;

  private readMagneticCardObservable: Observable<OutputReadMagneticCard>;
  private readMagneticCardSubscriber: Subscriber<OutputReadMagneticCard>;

  private readICCardObservable: Observable<OutputReadICCard>;
  private readICCardSubscriber: Subscriber<OutputReadICCard>;

  private readIDCardObservable: Observable<OutputReadIDCard>;
  private readIDCardSubscriber: Subscriber<OutputReadIDCard>;

  private readHealthCardObservable: Observable<OutputReadHealthCard>;
  private readHealthCardSubscriber: Subscriber<OutputReadHealthCard>;

  // 将日志写入外壳，提供给其他类使用
  static logToShell(data: string) {
    if (ShellRequestService.instance) {
      const inputLog: InputLog = new InputLog();
      inputLog.param = data;
      ShellRequestService.instance.web2ShellLog(inputLog);
    }
  }

  // 初始化日志，此类日志可被其他类捕获后进一步处理
  initLog(): Observable<InfoMessage> {
    this.logObservable = new Observable(
      subscriber => {
        this.logSubscriber = subscriber;
      }
    );
    return this.logObservable;
  }

  constructor() {
  }

  init() {
    ShellRequestService.instance = this;

    // 注册对外的接口
    window['angularComponentRef'] = {
      runThisFunctionFromOutside: this.runThisFunctionFromOutside.bind(this),
    };
  }

  // 被从外部调用的函数
  runThisFunctionFromOutside(type: string, data: string) {
    this.log(new InfoMessage('get message from shell, type:[' + type + '], data:[' + JSON.stringify(data) + ']'));

    switch (type) {
      case ShellApiLibrary.getConfig:
        this.shell2WebGetConfig(data);
        break;
      case ShellApiLibrary.readMagneticCard:
        this.shell2WebReadMagneticCard(data);
        break;
      case ShellApiLibrary.readICCard:
        this.shell2WebReadICCard(data);
        break;
      case ShellApiLibrary.readIDCard:
        this.shell2WebReadIDCard(data);
        break;
      case ShellApiLibrary.readHealthCard:
        this.shell2WebReadHealthCard(data);
        break;

      case ShellApiLibrary.test:
        this.shell2WebTest(data);
        break;

      default:
        this.log(new InfoMessage('unknown data from shell with code: ' + type + ', and data: ' + data, InfoMessage.statusError));
    }
  }

  // ------

  // 通知外壳，获取配置数据
  web2ShellGetConfig(): Observable<OutputGetConfig> {
    this.getConfigObservable = new Observable(
      subscriber => {
        this.getConfigSubscriber = subscriber;
      }
    );

    const dataContent: string = ShellUtility.generateString2Shell(ShellApiLibrary.getConfig, null);
    setTimeout(() => {
      this.log(new InfoMessage(
        'send message to shell:[' +
        dataContent +
        '], isShellDebugMode:[' +
        DeviceParameter.isShellDebugMode +
        ']'
      ));
      requestServiceInJS(dataContent, DeviceParameter.isShellDebugMode);
    }, 200);


    return this.getConfigObservable;
  }

  // 外壳回调，获取配置数据
  shell2WebGetConfig(data: string) {
    const valueArray: string[] = data.split(ShellUtility.protocolSeparator);
    if ((valueArray.length === 2) && (ShellApiLibrary.statusSuc === valueArray[0])) {
      const dataObject: OutputGetConfig = <OutputGetConfig>JSON.parse(valueArray[1]);

      if (this.getConfigSubscriber) {
        try {
          this.getConfigSubscriber.next(dataObject);
        } catch (err) {
          this.log(new InfoMessage('the subscriber.next() get error ...' +  err.stack, InfoMessage.statusError));
          this.getConfigSubscriber.error(ShellUtility.errorDataFromShell);
        }
      } else {
        this.log(new InfoMessage('the subscriber is null or undefined!', InfoMessage.statusError));
      }
    } else {
      this.getConfigSubscriber.error(ShellUtility.errorDataFromShell);
    }
  }

  // 通知外壳，日志记录
  web2ShellLog(data: InputLog) {
    const dataContent: string = ShellUtility.generateString2Shell4Log(ShellApiLibrary.log, data);
    requestServiceInJS(dataContent, DeviceParameter.isShellDebugMode);
  }

  // 通知外壳，读磁条卡
  web2ShellReadMagneticCard(): Observable<OutputReadMagneticCard> {
    this.readMagneticCardObservable = new Observable(
      subscriber => {
        this.readMagneticCardSubscriber = subscriber;
      }
    );

    return this.readMagneticCardObservable;
  }

  // 外壳回调，读磁条卡
  shell2WebReadMagneticCard(data: string) {
    this.log(new InfoMessage('get card from shell: ' +  data));
    if (DeviceParameter.isUserDebugMode) {
      // 模拟用户数据
      data = MockShell.mockReadCardData();
      this.log(new InfoMessage('get card from mock: ' +  data));
    }

    const valueArray: string[] = data.split(ShellUtility.protocolSeparator);
    if ((valueArray.length === 2) && (ShellApiLibrary.statusSuc === valueArray[0])) {
      const dataObject: OutputReadMagneticCard = <OutputReadMagneticCard>JSON.parse(valueArray[1]);

      if (this.readMagneticCardSubscriber) {
        try {
          this.readMagneticCardSubscriber.next(dataObject);
        } catch (err) {
          this.log(new InfoMessage('the subscriber.next() get error ...' +  err.stack, InfoMessage.statusError));
          this.readMagneticCardSubscriber.error(ShellUtility.errorDataFromShell);
        }
      } else {
        this.log(new InfoMessage('the subscriber is null or undefined!', InfoMessage.statusError));
      }
    } else {
      this.readMagneticCardSubscriber.error(ShellUtility.errorDataFromShell);
    }
  }

  // 通知外壳，读IC卡
  web2ShellReadICCard(): Observable<OutputReadICCard> {
    this.readICCardObservable = new Observable(
      subscriber => {
        this.readICCardSubscriber = subscriber;
      }
    );

    return this.readICCardObservable;
  }

  // 外壳回调，读IC卡
  shell2WebReadICCard(data: string) {
    const valueArray: string[] = data.split(ShellUtility.protocolSeparator);
    if ((valueArray.length === 2) && (ShellApiLibrary.statusSuc === valueArray[0])) {
      const dataObject: OutputReadICCard = <OutputReadICCard>JSON.parse(valueArray[1]);

      if (this.readICCardSubscriber) {
        try {
          this.readICCardSubscriber.next(dataObject);
        } catch (err) {
          this.log(new InfoMessage('the subscriber.next() get error ...' +  err.stack, InfoMessage.statusError));
          this.readICCardSubscriber.error(ShellUtility.errorDataFromShell);
        }
      } else {
        this.log(new InfoMessage('the subscriber is null or undefined!', InfoMessage.statusError));
      }
    } else {
      this.readICCardSubscriber.error(ShellUtility.errorDataFromShell);
    }
  }

  // 通知外壳，读身份证
  web2ShellReadIDCard(): Observable<OutputReadIDCard> {
    this.readIDCardObservable = new Observable(
      subscriber => {
        this.readIDCardSubscriber = subscriber;
      }
    );

    return this.readIDCardObservable;
  }

  // 外壳回调，读身份证
  shell2WebReadIDCard(data: string) {
    const valueArray: string[] = data.split(ShellUtility.protocolSeparator);
    if ((valueArray.length === 2) && (ShellApiLibrary.statusSuc === valueArray[0])) {
      const dataObject: OutputReadIDCard = <OutputReadIDCard>JSON.parse(valueArray[1]);

      if (this.readIDCardSubscriber) {
        try {
          this.readIDCardSubscriber.next(dataObject);
        } catch (err) {
          this.log(new InfoMessage('the subscriber.next() get error ...' +  err.stack, InfoMessage.statusError));
          this.readIDCardSubscriber.error(ShellUtility.errorDataFromShell);
        }
      } else {
        this.log(new InfoMessage('the subscriber is null or undefined!', InfoMessage.statusError));
      }
    } else {
      this.readIDCardSubscriber.error(ShellUtility.errorDataFromShell);
    }
  }

  // 通知外壳，读医保卡
  web2ShellReadHealthCard(): Observable<OutputReadHealthCard> {
    this.readHealthCardObservable = new Observable(
      subscriber => {
        this.readHealthCardSubscriber = subscriber;
      }
    );

    return this.readHealthCardObservable;
  }

  // 外壳回调，读医保卡
  shell2WebReadHealthCard(data: string) {
    const valueArray: string[] = data.split(ShellUtility.protocolSeparator);
    if ((valueArray.length === 2) && (ShellApiLibrary.statusSuc === valueArray[0])) {
      const dataObject: OutputReadHealthCard = <OutputReadHealthCard>JSON.parse(valueArray[1]);

      if (this.readHealthCardSubscriber) {
        try {
          this.readHealthCardSubscriber.next(dataObject);
        } catch (err) {
          this.log(new InfoMessage('the subscriber.next() get error ...' +  err.stack, InfoMessage.statusError));
          this.readHealthCardSubscriber.error(ShellUtility.errorDataFromShell);
        }
      } else {
        this.log(new InfoMessage('the subscriber is null or undefined!', InfoMessage.statusError));
      }
    } else {
      this.readHealthCardSubscriber.error(ShellUtility.errorDataFromShell);
    }
  }

  // 通知外壳，关闭软件
  web2ShellShutDown() {
    const dataContent: string = ShellUtility.generateString2Shell(ShellApiLibrary.shutDown, null);
    requestServiceInJS(dataContent, DeviceParameter.isShellDebugMode);
  }

  // 通知外壳，更新程序
  web2ShellUpdateVersion() {
    const dataContent: string = ShellUtility.generateString2Shell(ShellApiLibrary.updateVersion, null);
    requestServiceInJS(dataContent, DeviceParameter.isShellDebugMode);
  }

  // 通知外壳，设置自动开机时间
  web2ShellSetTimeForPowerOn(data: InputSetTimeForPowerOn) {
    const dataContent: string = ShellUtility.generateString2Shell(ShellApiLibrary.setTimeForPowerOn, data);
    requestServiceInJS(dataContent, DeviceParameter.isShellDebugMode);
  }

  // 通知外壳，设置自动关机时间
  web2ShellSetTimeForPowerOff(data: InputSetTimeForPowerOff) {
    const dataContent: string = ShellUtility.generateString2Shell(ShellApiLibrary.setTimeForPowerOff, data);
    requestServiceInJS(dataContent, DeviceParameter.isShellDebugMode);
  }

  // 外壳回调，测试
  shell2WebTest(data: string) {
    const valueArray: string[] = data.split(ShellUtility.protocolSeparator);
    if (valueArray.length === 2) {
      MockShell.mock(valueArray[0], valueArray[1]);
    } else {
      MockShell.mock(valueArray[0], null);
    }
  }

  // 记录日志，本类内部使用
  private log(data: InfoMessage) {
    this.logSubscriber.next(data);
  }
}



