// 模拟外壳数据

import {ShellUtility} from 'app/core/public/shell-utility';
import {ModeLibrary} from 'app/core/libs/mode-library';
import {ServerApiLibrary} from 'app/protocol/server/server-api-library';
import {ShellApiLibrary} from 'app/protocol/shell/shell-api-library';
import {OutputGetConfig} from 'app/protocol/shell/output-get-config';
import {OutputReadMagneticCard} from 'app/protocol/shell/output-read-magnetic-card';

import 'assets/external/external.js';

declare function responseService(type: string, data: string);  // shell回调web的接口函数，为了便于TS中代码编译检查

export class MockShell {

  private static readonly hospitalID_AQEY: string = 'aqsdermyyadmin'; // 安庆二院

  private static readonly server_WEB: string = 'http://web.witontek.com/eHospital2/'; // web
  private static readonly server_CYT: string = 'http://192.168.1.165:8080/eHospital2/'; // 陈远桃
  private static readonly server_LWL: string = 'http://192.168.1.161:8080/eHospital2/'; // 廖文蕾
  private static readonly server_PRO: string = 'http://ehospital.witontek.com/eHospital2/'; // 生产环境




  public static mock(type: string, data: string) {
    switch (type) {
      case ShellApiLibrary.getConfig:
        MockShell.mockShell2WebGetConfig();
        break;

      case ShellApiLibrary.readMagneticCard:
        MockShell.mockShell2WebReadMagneticCard();
        break;

      default:
        console.error('get web2shell data with code: ' + type + ', and data: ' + data);
    }
  }

  // ------------

  // 获取配置相关

  private static mockShell2WebGetConfig() {
    MockShell.mockShell2WebGetConfigSuc();
    // MockShell.mockShell2WebGetConfigErr();
  }

  private static mockShell2WebGetConfigSuc() {
    // 模拟异步行为
    setTimeout(() => {
      const outputGetConfig: OutputGetConfig = MockShell.getDemoHardwareInfo();
      const data: string = ShellUtility.generateString2web(ShellApiLibrary.statusSuc, outputGetConfig);
      responseService(ShellApiLibrary.getConfig, data);
    }, 500);
  }

  // private static mockShell2WebGetConfigErr() {
  //   // 模拟异步行为
  //   setTimeout(() => {
  //     const data: string = ShellUtility.generateString2web(ShellApiLibrary.statusErr, null);
  //     responseService(ShellApiLibrary.getConfig, data);
  //   }, 500);
  // }

  private static getDemoHardwareInfo(): OutputGetConfig {
    const outputGetConfig: OutputGetConfig = new OutputGetConfig();
    outputGetConfig.server = MockShell.server_WEB;
    outputGetConfig.deviceID = '';
    outputGetConfig.hospitalID = MockShell.hospitalID_AQEY;
    outputGetConfig.macAddress = 'aa:bb:cc:dd:ee';
    // outputGetConfig.macAddress = '68:F7:28:68:A7:9C';
    outputGetConfig.download = 'http://d2.witon.us/minipay/dev/test/download/dev/aqsdermyyadmin/download.json';
    outputGetConfig.webVersion = '0.1.5';
    outputGetConfig.mode = ModeLibrary.modeWebDebug;
    return outputGetConfig;
  }

  // ------------

  // 直接模拟外壳读卡数据，适合无壳状态下，用于主动触发模拟读卡动作
  private static mockShell2WebReadMagneticCard() {
    // 模拟异步行为
    setTimeout(() => {
      const data: string = MockShell.mockReadCardData();
      responseService(ShellApiLibrary.readMagneticCard, data);
    }, 100);

  }

  // ------------

  // 模拟环境数据，适合有壳状态下
  public static mockReadCardData(): string {

    const outputReadMagneticCard: OutputReadMagneticCard = new OutputReadMagneticCard();

    switch (ServerApiLibrary.authInfo.hospitalId) {
      case MockShell.hospitalID_AQEY:
        outputReadMagneticCard.cardID = '00321508';
        // outputReadMagneticCard.cardID = '17010650';
        break;

      default:
        console.error('hospitalID not found: ' + ServerApiLibrary.authInfo.hospitalId);
    }

    const data: string = ShellUtility.generateString2web(ShellApiLibrary.statusSuc, outputReadMagneticCard);
    return data;
  }

}



