// 用户信息

import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';

export class UserInfo {

  outputQueryOutPatientInfo: OutputQueryOutPatientInfo; // 门诊病人
  outputQueryInHospitalPatient: OutputQueryInHospitalPatient; // 住院病人

  /**
   * 判断是否存在用户信息
   * @returns {boolean}
   */
  hasPatientInfo(): boolean {
    let hasInfo = false;
    if ((this.outputQueryOutPatientInfo) || (this.outputQueryInHospitalPatient)) {
      hasInfo = true;
    }
    return hasInfo;
  }

  /**
   * 清除用户信息
   */
  clearPatientInfo() {
    this.outputQueryOutPatientInfo = null;
    this.outputQueryInHospitalPatient = null;
  }
}



