// loading处理，应显示在界面上

import { Injectable } from '@angular/core';
import {Observable, Subscriber} from 'rxjs/Rx';

import {BaseService} from 'app/core/bases/base-service';

@Injectable()
export class LoadingService extends BaseService {

  private loadingObservable: Observable<boolean>; // 提供给外部的观察对象，系统错误信息
  private loadingSubscriber: Subscriber<boolean>;

  constructor() {
    super();
  }

  /**
   * 获取Observable，UI组件专用
   * @returns {Observable<boolean>}
   */
  public getLoading(): Observable<boolean> {
    return this.loadingObservable;
  }

  /**
   * 显示loading
   */
  public showLoading() {
    this.loadingSubscriber.next(true);
  }

  /**
   * 显示loading
   */
  public hiddenLoading() {
    this.loadingSubscriber.next(false);
  }

  init() {
    this.loadingObservable = new Observable(
      subscriber => {
        this.loadingSubscriber = subscriber;
      }
    );
  }

}



