// 封装了出错的处理，应显示在界面上

import { Injectable } from '@angular/core';
import {Observable, Subscriber} from 'rxjs/Rx';

import {BaseService} from 'app/core/bases/base-service';
import {InfoMessage} from 'app/core/objects/info-message';

@Injectable()
export class ErrorService extends BaseService {

  private errorObservable: Observable<InfoMessage>; // 提供给外部的观察对象，系统错误信息
  private errorSubscriber: Subscriber<InfoMessage>;

  constructor() {
    super();
  }

  /**
   * 获取Observable，UI组件专用
   * @returns {Observable<InfoMessage>}
   */
  public getError(): Observable<InfoMessage> {
    return this.errorObservable;
  }

  /**
   * 显示错误信息
   * @param {InfoMessage} infoMessage
   */
  public showError(infoMessage: InfoMessage) {
    this.errorSubscriber.next(infoMessage);
  }

  init() {
    this.errorObservable = new Observable(
      subscriber => {
        this.errorSubscriber = subscriber;
      }
    );
  }

}



