import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {SharedModule} from './../shared/shared.module';

import {MatButtonModule, MatCheckboxModule, MatDialogModule, MatRadioModule} from '@angular/material';
import {SwiperModule} from 'ngx-swiper-wrapper';

import {LoadingComponent} from './loading/loading.component';
import {ReadCardComponent} from './read-card/read-card.component';
import {MainMenuComponent} from './main-menu/main-menu.component';
import {MainMenuItemComponent} from './main-menu-item/main-menu-item.component';
import {OutpatientInfoComponent} from './outpatient-info/outpatient-info.component';
import {BannerComponent} from './banner/banner.component';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    SharedModule,
    MatButtonModule,
    MatCheckboxModule,
    MatDialogModule,
    MatRadioModule,
    SwiperModule
  ],
  declarations: [
    LoadingComponent,
    ReadCardComponent,
    MainMenuComponent,
    MainMenuItemComponent,
    OutpatientInfoComponent,
    BannerComponent
  ],
  exports: []
})
export class HomeModule {
}



