// 门诊缴费记录
import {Component, OnInit} from '@angular/core';

import {PathLibrary} from 'app/core/libs/path-library';
import {LogService} from 'app/core/services/log.service';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';
import {RouterService} from 'app/core/services/router.service';
import {OutputQueryOutpatientPayRecords} from 'app/protocol/server/output-query-outpatient-pay-records';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';

import {UserService} from 'app/core/services/user.service';
import {InfoMessage} from 'app/core/objects/info-message';
import {InfoLibrary} from 'app/core/libs/info-library';
import {SystemService} from 'app/core/services/system.service';

@Component({
  selector: 'app-outpatient-payment-record',
  templateUrl: './outpatient-payment-record.component.html',
  styleUrls: ['./outpatient-payment-record.component.css']
})
export class OutpatientPaymentRecordComponent implements OnInit {
  outpatientInfo: OutputQueryOutPatientInfo;
  outputQueryOutpatientPayRecords = new OutputQueryOutpatientPayRecords(); // 门诊缴费记录（未缴费用）
  billAmount = ''; // 费用账单总金额
  debt = ''; // 需要补缴的金额
  tipsShow = false;

  billListEmpty = false;

  constructor(private routerService: RouterService,
              private userService: UserService,
              private systemService: SystemService,
              private errorService: ErrorService,
              private loadingService: LoadingService) {
  }

  ngOnInit() {
    this.outpatientInfo = this.userService.getOutPatientInfo();
    this.getOutpatientPayRecords();
  }

  goToPaymentAmountSelect(): void {
    const debtObj = {debt: this.debt};
    this.routerService.goTo([PathLibrary.paymentAmountSelect, debtObj]);
  }

  // 从服务器获取门诊缴费记录（未缴费用）
  private getOutpatientPayRecords(): void {
    this.loadingService.showLoading();

    this.userService.getOutpatientPayRecordsFromServer().subscribe(
      data => this.getOutpatientPayRecordsSuc(data),
      error => this.getOutpatientPayRecordsErr(error)
    );
  }

  // 从服务器获取门诊缴费记录（未缴费用），成功
  private getOutpatientPayRecordsSuc(outputQueryOutpatientPayRecords: OutputQueryOutpatientPayRecords): void {
    LogService.debug('getOutpatientPayRecordsSuc: ' + outputQueryOutpatientPayRecords);

    this.outputQueryOutpatientPayRecords = outputQueryOutpatientPayRecords;
    if (this.outputQueryOutpatientPayRecords.list) {
      this.countBillAmount();
    } else {
      this.billListEmpty = true;
    }
    console.dir(this.outputQueryOutpatientPayRecords);

    this.loadingService.hiddenLoading();
  }

  // 从服务器获取门诊缴费记录（未缴费用），失败
  private getOutpatientPayRecordsErr(errorMsg: string): void {
    LogService.error('getOutpatientPayRecordsErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(errorMsg);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }

  // 计算总金额
  private countBillAmount(): void {
    let amount = 0;
    const balance_amt = parseFloat(this.outputQueryOutpatientPayRecords.balance_amt);
    // const balance_amt = 0;
    for (let i = 0; i < this.outputQueryOutpatientPayRecords.list.length; i++) {
      amount += parseFloat(this.outputQueryOutpatientPayRecords.list[i].order_amount);
    }
    amount = Math.round(amount * 100) / 100;

    this.billAmount = amount.toString();
    if (balance_amt < amount) {
      this.tipsShow = true;
      const debt = amount - balance_amt;
      this.debt = (Math.round(debt * 100) / 100).toString();
    }
  }
}



