// 主菜单按钮
import {Component, Input, OnInit} from '@angular/core';

import {CoverItem} from 'app/core/objects/cover-item';
import {CoverItemLibrary} from 'app/core/libs/cover-item-library';
import {RouterService} from 'app/core/services/router.service';

@Component({
  selector: 'app-main-menu-item',
  templateUrl: './main-menu-item.component.html',
  styleUrls: ['./main-menu-item.component.css']
})

export class MainMenuItemComponent implements OnInit {

  @Input() itemType: number;

  currentItemData: CoverItem;

  constructor(private routerService: RouterService) {
  }

  ngOnInit() {
    // get item data
    this.currentItemData = CoverItemLibrary.itemData()[(this.itemType - 1)];
  }

  goToDetail(): void {
    this.routerService.goTo([this.currentItemData.path]);
  }

}



