// @see: ServerApiLibrary.queryInHospitalPatient
export class OutputQueryInHospitalPatient {
  patient_card: string; // 卡号
  real_name: string; // 姓名
  his_pat_id: string; // HIS就诊人ID
  serial_number: string; // 住院号
  gender: string; // 性别
  department_name: string; // 科室名称
  doctor_name: string; // 医生名称
  inpatient_area: string; // 病区
  birthday: string; // 生日
  totle_amt: string; // 预交金额累计充值
  used_amt: string; // 已使用费用
  remain_amt: string; // 预交金余额
}



