// 服务器接口定义，由工具生成

import {AuthInfo} from 'app/core/objects/auth-info';

export class ServerApiLibrary {

  private static _authInfo: AuthInfo = new AuthInfo();

  static get authInfo(): AuthInfo {
    return this._authInfo;
  }

  static set authInfo(value: AuthInfo) {
    this._authInfo = value;
  }

  // -----------

  public static readonly deviceLogin = 'minipay/auth/login'; // 设备登陆
  public static readonly queryPayChannelList = 'minipay/channel/qryPayChannelList'; // 商户支付渠道查询
  public static readonly queryOutPatientInfo = 'minipay/patient/qryOutPatientInfo'; // 查询门诊病人信息
  public static readonly queryInHospitalPatient = 'minipay/patient/qryInhospitalPatient'; // 查询住院病人信息
  public static readonly createOrder = 'minipay/order/createOrder'; // 缴费下单（生成预订单）
  public static readonly unifiedOrder = 'minipay/order/UnifiedOrder'; // 统一支付渠道下单
  public static readonly queryOrderStatus = 'minipay/order/qryOrderStatus'; // 查询订单状态
  public static readonly queryPaidRecord = 'minipay/order/qryPaidRecord'; // 查询缴费记录
  public static readonly queryOutpatientPayRecords = 'minipay/outpatient/qryOutpatientPayRecords'; // 门诊缴费记录（未缴费用）

}



