// 错误信息弹框
import {Component, Inject, OnInit, OnDestroy} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';
import {TimerObservable} from 'rxjs/observable/TimerObservable';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';

@Component({
  selector: 'app-error-dialog',
  templateUrl: './error-dialog.component.html',
  styleUrls: ['./error-dialog.component.css']
})
export class ErrorDialogComponent implements OnInit, OnDestroy {

  private timerObservable: Observable<number>; // 计时器，内容使用
  private timerSubscription: Subscription;

  leftBtn = '取消';
  rightBtn = '确定';

  constructor(public dialogRef: MatDialogRef<ErrorDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any) {
  }

  ngOnInit() {
    if (this.data.leftBtn) {
      this.leftBtn = this.data.leftBtn;
    }
    if (this.data.rightBtn) {
      this.rightBtn = this.data.rightBtn;
    }

    this.doAutoClose(this.data.autoCloseTime);
  }

  ngOnDestroy() {
    if (this.timerSubscription) {
      this.timerSubscription.unsubscribe();
    }
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  // 处理自动关闭
  private doAutoClose(autoCloseTime: number): void {
    if (autoCloseTime > 0) {
      this.timerObservable = TimerObservable.create(autoCloseTime, autoCloseTime);
      this.timerSubscription = this.timerObservable.subscribe(
        t => {
          console.log('t: ', t);
          this.dialogRef.close();
        });
    }
  }
}



