// 信息消息

import {RouterInfo} from 'app/core/objects/router-info';

export class InfoMessage {

  static readonly statusNormal: number = 1; // 普通信息
  static readonly statusError: number = 2; // 错误信息

  data: string; // 数据内容
  status: number; // 普通信息，错误信息
  routerInfo: RouterInfo; // 需要跳转的路由信息
  autoCloseTime = 0; // 打开后自动关闭前持续的时间，0为不会自动关闭，大于零则为持续时间，单位毫秒
  noRouter = false; // 关闭时是否无须跳转到其他UI，如为 true 则不会跳转

  constructor(data: string,
              status: number = 1,
              routerInfo: RouterInfo = null) {
    this.data = data;
    this.status = status;
    this.routerInfo = routerInfo;
  }
}



