// @see: ServerApiLibrary.createOrder
export class InputCreateOrder {
  hospital_id: string; // 医院ID
  totle_amt: string; // 缴费总金额
  visit_id: string; // 批次号
  patient_id: string; // 就诊人ID
  real_name: string; // 就诊人姓名
  id_card: string; // 就诊人身份证号
  data_src: string; // 缴费类型
  biz_id: string; // 业务主键
  pay_type: string; // 支付渠道
}



