// 路由的封装，含有用户权限认证的功能

import {Injectable} from '@angular/core';
import {NavigationExtras, Router} from '@angular/router';

import {BaseService} from 'app/core/bases/base-service';
import {PathLibrary} from 'app/core/libs/path-library';
import {LogService} from 'app/core/services/log.service';
import {UserService} from 'app/core/services/user.service';

import {RouterInfo} from 'app/core/objects/router-info';

@Injectable()
export class RouterService extends BaseService {

  private whiteList: string[] = []; // 白名单，不需要身份认证的页面
  private routerTrace: RouterInfo[] = []; // 路由足迹

  constructor(private router: Router,
              private userService: UserService) {
    super();
  }

  init() {
    this.whiteList.push(PathLibrary.loading);
    this.whiteList.push(PathLibrary.errorPage);
    this.whiteList.push(PathLibrary.errorWait);
    this.whiteList.push(PathLibrary.cover);
    this.whiteList.push(PathLibrary.ad);
    this.whiteList.push(PathLibrary.mainMenu);
    this.whiteList.push(PathLibrary.inpatientLogin);
    this.whiteList.push(PathLibrary.banner);
  }

  /**
   * 跳转 UI。如无访问权限，则会自动跳转到设定UI
   */
  goTo(commands: any[], extras?: NavigationExtras) {

    LogService.debug('router want goto: [' + String(commands) + ']');

    if (commands.length >= 1) {
      // 如果不在白名单中，则一律跳转到读卡界面，读卡完成后重新跳转到想去的界面
      if (this.whiteList.indexOf(commands[0]) < 0) {
        // 不在白名单中
        if (!this.userService.hasPatientInfo()) {
          // 身份认证未通过，跳到读卡界面，并保留想访问的地址
          commands[0] = PathLibrary.readCardPath + commands[0];
        }
      }

      // save trace
      const currentRouterInfo: RouterInfo = new RouterInfo(commands, extras);
      this.routerTrace.push(currentRouterInfo);

      // navigate
      if (typeof extras === 'object' && extras !== null) {
        this.router.navigate(currentRouterInfo.commands, currentRouterInfo.extras);
        LogService.debug('router goto: [' + String(currentRouterInfo.commands) + ', extras:' + String(currentRouterInfo.extras) + ']');
      } else {
        this.router.navigate(currentRouterInfo.commands);
        LogService.debug('router goto: [' + String(currentRouterInfo.commands) + ']');
      }

    } else {
      LogService.error('Router parameter error because the commands value is wrong');
    }

  }

  /**
   * 后退
   */
  back() {
    this.routerTrace.pop(); // 去掉当前路由信息
    const currentRouterInfo: RouterInfo = this.routerTrace.pop(); // 取出上次路由信息
    this.goTo(currentRouterInfo.commands, currentRouterInfo.extras); // 重做上次路由跳转
    LogService.debug('router goto: [' + String(currentRouterInfo.commands) + ']');
  }

  /**
   * 清除路由痕迹
   */
  clearTrace() {
    this.routerTrace = [];
  }

}



