// 封装了用户的行为

import {Observable, Subscriber} from 'rxjs/Rx';

import {Injectable} from '@angular/core';
import {LogService} from 'app/core/services/log.service';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';
import {ResponseDataWrap} from 'app/core/objects/response-data-wrap';
import {UserInfo} from 'app/core/objects/user-info';
import {InputQueryInHospitalPatient} from 'app/protocol/server/input-query-in-hospital-patient';
import {InputQueryOutPatientInfo} from 'app/protocol/server/input-query-out-patient-info';
import {InputQueryOutpatientPayRecords} from 'app/protocol/server/input-query-outpatient-pay-records';
import {InputCreateOrder} from 'app/protocol/server/input-create-order';
import {InputUnifiedOrder} from 'app/protocol/server/input-unified-order';
import {InputQueryOrderStatus} from 'app/protocol/server/input-query-order-status';
import {ServerRequestService} from 'app/protocol/server/server-request.service';
import {ServerApiLibrary} from 'app/protocol/server/server-api-library';
import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {OutputQueryOutpatientPayRecords} from 'app/protocol/server/output-query-outpatient-pay-records';
import {OutputCreateOrder} from 'app/protocol/server/output-create-order';
import {OutputUnifiedOrder} from 'app/protocol/server/output-unified-order';
import {OutputQueryOrderStatus} from 'app/protocol/server/output-query-order-status';
import {PayLibrary} from 'app/core/libs/pay-library';
import {InfoLibrary} from 'app/core/libs/info-library';
import {PublicLibrary} from 'app/core/libs/public-library';

@Injectable()
export class UserService {

  private userInfo: UserInfo = new UserInfo(); // 赋除值，因此不会为空对象

  private getOutPatientInfoFromServerObservable: Observable<OutputQueryOutPatientInfo>;
  private getOutPatientInfoFromServerSubscriber: Subscriber<OutputQueryOutPatientInfo>;

  private getOutpatientPayRecordsFromServerObservable: Observable<OutputQueryOutpatientPayRecords>;
  private getOutpatientPayRecordsFromServerSubscriber: Subscriber<OutputQueryOutpatientPayRecords>;

  private createOrderByServerObservable: Observable<OutputCreateOrder>;
  private createOrderByServerSubscriber: Subscriber<OutputCreateOrder>;

  private unifiedOrderByServerObservable: Observable<OutputUnifiedOrder>;
  private unifiedOrderByServerSubscriber: Subscriber<OutputUnifiedOrder>;

  private queryOrderStatusFromServerObservable: Observable<OutputQueryOrderStatus>;
  private queryOrderStatusFromServerSubscriber: Subscriber<OutputQueryOrderStatus>;

  private getInpatientInfoFromServerObservable: Observable<OutputQueryInHospitalPatient>;
  private getInpatientInfoFromServerSubscriber: Subscriber<OutputQueryInHospitalPatient>;

  constructor(private serverRequestService: ServerRequestService,
              private errorService: ErrorService,
              private loadingService: LoadingService) {
  }

  /**
   * 是否用户信息已获取
   * @returns {boolean}
   */
  hasPatientInfo(): boolean {
    return this.userInfo.hasPatientInfo();
  }

  /**
   * 清除用户信息
   */
  clearUserInfo() {
    this.userInfo.clearPatientInfo();
  }

  /**
   * 获取就诊人信息
   * @returns {OutputQueryOutPatientInfo}
   */
  getOutPatientInfo(): OutputQueryOutPatientInfo {
    return this.userInfo.outputQueryOutPatientInfo;
  }

  /**
   * 获取住院病人信息
   * @returns {OutputQueryInHospitalPatient}
   */
  getInPatientInfo(): OutputQueryInHospitalPatient {
    return this.userInfo.outputQueryInHospitalPatient;
  }

  // -----------------------------------------------------------------------------

  /**
   * 从服务器获取门诊病人的信息
   * @param {string} patientNum 就诊卡号
   * @returns {Observable<OutputQueryOutPatientInfo>}
   */
  getOutPatientInfoFromServer(patientNum: string): Observable<OutputQueryOutPatientInfo> {
    this.getOutPatientInfoFromServerObservable = new Observable(
      subscriber => {
        this.getOutPatientInfoFromServerSubscriber = subscriber;
      }
    );

    const inputQueryOutPatientInfo: InputQueryOutPatientInfo = new InputQueryOutPatientInfo();
    inputQueryOutPatientInfo.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputQueryOutPatientInfo.patient_card = patientNum;
    setTimeout(() => {
      this.serverRequestService.queryOutPatientInfo(inputQueryOutPatientInfo)
        .subscribe(
          data => this.getOutPatientInfoFromServerSuc(data),
          error => this.getOutPatientInfoFromServerErr(error)
        );
    }, 200);

    return this.getOutPatientInfoFromServerObservable;
  }

  // 从服务器获取门诊病人的信息，成功
  private getOutPatientInfoFromServerSuc(responseDataWrap: ResponseDataWrap): void {
    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {
      // save
      this.userInfo.outputQueryOutPatientInfo =
        Object.assign(new OutputQueryOutPatientInfo(), responseDataWrap.responseData);
      LogService.debug('getOutPatientInfoFromServerSuc: ' + this.userInfo.outputQueryOutPatientInfo.real_name);

      this.getOutPatientInfoFromServerSubscriber.next(this.userInfo.outputQueryOutPatientInfo);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseTokenTimeout) {
      LogService.error('getOutPatientInfoFromServerErr: [token time out]');
      this.getOutPatientInfoFromServerSubscriber.error(InfoLibrary.networkError);

    } else {
      this.getOutPatientInfoFromServerSubscriber.error(responseDataWrap.responseMessage);
    }
  }

  private getOutPatientInfoFromServerErr(errorMsg: string): void {
    LogService.error('getOutPatientInfoFromServerErr: [' + errorMsg + ']');
    this.getOutPatientInfoFromServerSubscriber.error(InfoLibrary.networkError);
  }

  // -----------------------------------------------------------------------------

  /**
   * 获取门诊缴费记录（未缴费用）
   * @returns {Observable<OutputQueryOutpatientPayRecords>}
   */
  getOutpatientPayRecordsFromServer(): Observable<OutputQueryOutpatientPayRecords> {
    this.getOutpatientPayRecordsFromServerObservable = new Observable(
      subscriber => {
        this.getOutpatientPayRecordsFromServerSubscriber = subscriber;
      }
    );

    const inputQueryOutpatientPayRecords: InputQueryOutpatientPayRecords = new InputQueryOutpatientPayRecords();
    inputQueryOutpatientPayRecords.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputQueryOutpatientPayRecords.patient_card = this.userInfo.outputQueryOutPatientInfo.patient_card;
    setTimeout(() => {
      this.serverRequestService.queryOutpatientPayRecords(inputQueryOutpatientPayRecords)
        .subscribe(
          data => this.getOutpatientPayRecordsFromServerSuc(data),
          error => this.getOutpatientPayRecordsFromServerErr(error)
        );
    }, 200);

    return this.getOutpatientPayRecordsFromServerObservable;
  }

  private getOutpatientPayRecordsFromServerSuc(responseDataWrap: ResponseDataWrap): void {
    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {
      const outpatientPayRecords = Object.assign(new OutputQueryOutpatientPayRecords(), responseDataWrap.responseData);
      LogService.debug('getOutpatientPayRecordsFromServerSuc: ' + responseDataWrap.toString());

      this.getOutpatientPayRecordsFromServerSubscriber.next(outpatientPayRecords);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseTokenTimeout) {
      LogService.error('getOutpatientPayRecordsFromServerErr: [token time out]');
      this.getOutpatientPayRecordsFromServerSubscriber.error(InfoLibrary.networkError);

    } else {
      this.getOutpatientPayRecordsFromServerSubscriber.error(responseDataWrap.responseMessage);
    }
  }

  private getOutpatientPayRecordsFromServerErr(errorMsg: string): void {
    LogService.error('getOutpatientPayRecordsFromServerErr: [' + errorMsg + ']');
    this.getOutpatientPayRecordsFromServerSubscriber.error(InfoLibrary.networkError);
  }

  // -----------------------------------------------------------------------------

  /**
   * 缴费下单（生成预订单）
   * @param {string} totalAmt
   * @param {string} dataSrc
   * @param {string} payType
   * @returns {Observable<OutputCreateOrder>}
   */
  createOrderByServer(totalAmt: string, dataSrc: string, payType: string): Observable<OutputCreateOrder> {
    this.createOrderByServerObservable = new Observable(
      subscriber => {
        this.createOrderByServerSubscriber = subscriber;
      }
    );

    const inputCreateOrder: InputCreateOrder = new InputCreateOrder();
    if (dataSrc === PayLibrary.dataSrcInpatient) {
      inputCreateOrder.patient_id = this.userInfo.outputQueryInHospitalPatient.serial_number;
    } else {
      inputCreateOrder.patient_id = this.userInfo.outputQueryOutPatientInfo.patient_card;
    }
    inputCreateOrder.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputCreateOrder.totle_amt = totalAmt;
    inputCreateOrder.data_src = dataSrc;
    inputCreateOrder.pay_type = payType;
    setTimeout(() => {
      this.serverRequestService.createOrder(inputCreateOrder)
        .subscribe(
          data => this.createOrderByServerSuc(data),
          error => this.createOrderByServerErr(error)
        );
    }, 200);

    return this.createOrderByServerObservable;
  }

  private createOrderByServerSuc(responseDataWrap: ResponseDataWrap): void {
    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {
      const orderInfo = Object.assign(new OutputCreateOrder(), responseDataWrap.responseData);
      LogService.debug('createOrderByServerSuc: ' + responseDataWrap.toString());

      this.createOrderByServerSubscriber.next(orderInfo);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseTokenTimeout) {
      LogService.error('createOrderByServerErr: [token time out]');
      this.createOrderByServerSubscriber.error(InfoLibrary.networkError);

    } else {
      this.createOrderByServerSubscriber.error(responseDataWrap.responseMessage);
    }
  }

  private createOrderByServerErr(errorMsg: string): void {
    LogService.error('createOrderByServerErr: [' + errorMsg + ']');
    this.createOrderByServerSubscriber.error(InfoLibrary.networkError);
  }

  // -----------------------------------------------------------------------------

  /**
   * 统一支付渠道下单
   * @param {string} id
   * @param {string} tradeNo
   * @param {string} orderAmount
   * @returns {Observable<OutputUnifiedOrder>}
   */
  unifiedOrderByServer(id: string, tradeNo: string, orderAmount: string): Observable<OutputUnifiedOrder> {
    this.unifiedOrderByServerObservable = new Observable(
      subscriber => {
        this.unifiedOrderByServerSubscriber = subscriber;
      }
    );

    const inputUnifiedOrder: InputUnifiedOrder = new InputUnifiedOrder();
    inputUnifiedOrder.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputUnifiedOrder.id = id;
    inputUnifiedOrder.trade_no = tradeNo;
    inputUnifiedOrder.order_amount = orderAmount;
    setTimeout(() => {
      this.serverRequestService.unifiedOrder(inputUnifiedOrder)
        .subscribe(
          data => this.unifiedOrderByServerSuc(data),
          error => this.unifiedOrderByServerErr(error)
        );
    }, 200);

    return this.unifiedOrderByServerObservable;
  }

  private unifiedOrderByServerSuc(responseDataWrap: ResponseDataWrap): void {
    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {
      const orderInfo = Object.assign(new OutputUnifiedOrder(), responseDataWrap.responseData);
      LogService.debug('unifiedOrderByServerSuc: ' + responseDataWrap.toString());

      this.unifiedOrderByServerSubscriber.next(orderInfo);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseTokenTimeout) {
      LogService.error('unifiedOrderByServerErr: [token time out]');
      this.unifiedOrderByServerSubscriber.error(InfoLibrary.networkError);

    } else {
      this.unifiedOrderByServerSubscriber.error(responseDataWrap.responseMessage);
    }
  }

  private unifiedOrderByServerErr(errorMsg: string): void {
    LogService.error('unifiedOrderByServerErr: [' + errorMsg + ']');
    this.unifiedOrderByServerSubscriber.error(InfoLibrary.networkError);
  }

  // -----------------------------------------------------------------------------

  /**
   * 查询订单状态
   * @param {string} tradeNo
   * @returns {Observable<OutputQueryOrderStatus>}
   */
  queryOrderStatusFromServer(tradeNo: string): Observable<OutputQueryOrderStatus> {
    this.queryOrderStatusFromServerObservable = new Observable(
      subscriber => {
        this.queryOrderStatusFromServerSubscriber = subscriber;
      }
    );

    const inputQueryOrderStatus: InputQueryOrderStatus = new InputQueryOrderStatus();
    inputQueryOrderStatus.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputQueryOrderStatus.trade_no = tradeNo;
    setTimeout(() => {
      this.serverRequestService.queryOrderStatus(inputQueryOrderStatus)
        .subscribe(
          data => this.queryOrderStatusFromServerSuc(data),
          error => this.queryOrderStatusFromServerErr(error)
        );
    }, 200);

    return this.queryOrderStatusFromServerObservable;
  }

  private queryOrderStatusFromServerSuc(responseDataWrap: ResponseDataWrap): void {
    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {
      const orderInfo = Object.assign(new OutputQueryOrderStatus(), responseDataWrap.responseData);
      LogService.debug('queryOrderStatusFromServerSuc: ' + responseDataWrap.toString());

      this.queryOrderStatusFromServerSubscriber.next(orderInfo);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseTokenTimeout) {
      LogService.error('queryOrderStatusFromServerErr: [token time out]');
      this.queryOrderStatusFromServerSubscriber.error(InfoLibrary.networkError);

    } else {
      this.queryOrderStatusFromServerSubscriber.error(responseDataWrap.responseMessage);
    }
  }

  private queryOrderStatusFromServerErr(errorMsg: string): void {
    LogService.error('queryOrderStatusFromServerErr: [' + errorMsg + ']');
    this.queryOrderStatusFromServerSubscriber.error(InfoLibrary.networkError);
  }

  /**
   * 从服务器获取住院病人的信息
   * @param {string} serialNumber
   * @returns {Observable<OutputQueryInHospitalPatient>}
   */
  getInpatientInfoFromServer(serialNumber: string): Observable<OutputQueryInHospitalPatient> {
    this.getInpatientInfoFromServerObservable = new Observable(
      subscriber => {
        this.getInpatientInfoFromServerSubscriber = subscriber;
      }
    );

    const inputQueryInHospitalPatient: InputQueryInHospitalPatient = new InputQueryInHospitalPatient();
    inputQueryInHospitalPatient.hospital_id = ServerApiLibrary.authInfo.hospitalId;
    inputQueryInHospitalPatient.serial_number = serialNumber;
    setTimeout(() => {
      this.serverRequestService.queryInHospitalPatient(inputQueryInHospitalPatient)
        .subscribe(
          data => this.getInpatientInfoFromServerSuc(data),
          error => this.getInpatientInfoFromServerErr(error)
        );
    }, 200);

    return this.getInpatientInfoFromServerObservable;
  }

  // 从服务器获取住院病人的信息，成功
  private getInpatientInfoFromServerSuc(responseDataWrap: ResponseDataWrap): void {
    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {
      // save
      this.userInfo.outputQueryInHospitalPatient =
        Object.assign(new OutputQueryInHospitalPatient(), responseDataWrap.responseData);
      LogService.debug('getInpatientInfoFromServerSuc: ' + this.userInfo.outputQueryInHospitalPatient.real_name);

      this.getInpatientInfoFromServerSubscriber.next(this.userInfo.outputQueryInHospitalPatient);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseTokenTimeout) {
      LogService.error('getInpatientInfoFromServerErr: [token time out]');
      this.getInpatientInfoFromServerSubscriber.error(InfoLibrary.networkError);

    } else {
      this.getInpatientInfoFromServerSubscriber.error(responseDataWrap.responseMessage);
    }
  }

  private getInpatientInfoFromServerErr(errorMsg: string): void {
    LogService.error('getInpatientInfoFromServerErr: [' + errorMsg + ']');
    this.getInpatientInfoFromServerSubscriber.error(InfoLibrary.networkError);
  }
}



