// 门诊病人信息展示页
import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

import {RouterService} from 'app/core/services/router.service';

import {SystemService} from 'app/core/services/system.service';
import {UserService} from 'app/core/services/user.service';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';

@Component({
  selector: 'app-outpatient-info',
  templateUrl: './outpatient-info.component.html',
  styleUrls: ['./outpatient-info.component.css']
})
export class OutpatientInfoComponent implements OnInit {
  @Input() outpatientInfoShow: boolean;
  @Input() nextPath: string;
  @Output() outpatientInfoShowChange = new EventEmitter<boolean>();

  outpatientInfo = new OutputQueryOutPatientInfo();

  constructor(private routerService: RouterService,
              private systemService: SystemService,
              private userService: UserService) {
  }

  ngOnInit() {
    this.outpatientInfo = this.userService.getOutPatientInfo();
  }

  // 确认按钮点击
  confirmTap(): void {
    this.routerService.goTo([this.nextPath]);
  }

  // 虚拟键盘取消按钮点击
  cancelTap(): void {
    this.outpatientInfoShow = false;
    this.systemService.backMainMenu();
    this.outpatientInfoShowChange.emit(this.outpatientInfoShow);
  }
}



