// 与外壳交互时的工具集

import {ShellApiLibrary} from 'app/protocol/shell/shell-api-library';

export class ShellUtility {

  public static readonly errorDataFromShell = 'data error from shell';
  public static readonly protocolSeparator = '^*^'; // 协议中使用
  public static readonly logProtocolSeparator = '@@'; // 写入设备日志中使用
  private static readonly nullObject = '{}';

  /**
   * 生成符合通讯规则的字符串，提供给外壳使用
   * @param {string} action 与外壳约定的协议号
   * @param {Object} data 与外壳约定的协议数据类型，无则可为 null
   * @returns {string} 拼接完后的字符串，形如 '5100^*^{"name":"abc,"id:"a1"}'
   */
  public static generateString2Shell(action: string, data: object): string {
    let data2String: string;
    if (data) {
      data2String = JSON.stringify(data);
    } else {
      data2String = ShellUtility.nullObject;
    }
    data2String = action + ShellUtility.protocolSeparator + data2String;

    return data2String;
  }

  /**
   * 生成符合通讯规则的字符串，专为日志记录的格式，提供给外壳使用
   * 将 '^*^' 一律替换为 '@@'
   * @param {string} action 与外壳约定的协议号
   * @param {Object} data 与外壳约定的协议数据类型，无则可为 null
   * @returns {string}
   */
  public static generateString2Shell4Log(action: string, data: object): string {
    let data2String: string = ShellUtility.generateString2Shell(action, data);
    data2String = data2String.replace(ShellApiLibrary.log + ShellUtility.protocolSeparator, '###'); // 保护正常字符
    data2String = data2String.replace(ShellUtility.protocolSeparator, ShellUtility.logProtocolSeparator); // 替换掉可能出错的字符
    data2String = data2String.replace('###', ShellApiLibrary.log + ShellUtility.protocolSeparator); // 将受保护的字符替换回来
    return data2String;
  }

  /**
   * 生成符合通讯规则的字符串，提供给web使用，用于模拟shell传来的数据
   * @param {string} status 与外壳约定的协议号
   * @param {Object} data 与外壳约定的协议数据类型，无则可为 null
   * @returns {string} 拼接完后的字符串，形如 '0000^*^{"name":"abc,"id:"a1"}'
   */
  public static generateString2web(status: string, data: object): string {
    let data2String: string;
    if (data) {
      data2String = JSON.stringify(data);
    } else {
      data2String = ShellUtility.nullObject;
    }

    data2String = status + ShellUtility.protocolSeparator + data2String;

    return data2String;
  }
}



