// 缴费金额选择页面
import {Component, OnInit} from '@angular/core';

import {PathLibrary} from 'app/core/libs/path-library';
import {ErrorService} from 'app/core/services/error.service';
import {RouterService} from 'app/core/services/router.service';
import {ActivatedRoute} from '@angular/router';

import {UserService} from 'app/core/services/user.service';
import {InfoMessage} from 'app/core/objects/info-message';
import {PayLibrary} from 'app/core/libs/pay-library';
import {InfoLibrary} from "../../core/libs/info-library";

@Component({
  selector: 'app-payment-amount-select',
  templateUrl: './payment-amount-select.component.html',
  styleUrls: ['./payment-amount-select.component.css']
})
export class PaymentAmountSelectComponent implements OnInit {

  amountSting = '请输入缴费金额';
  totalAmount = '100';
  shortcutKey = '100';

  debt = ''; // 应缴金额
  balanceAmount = ''; // 当前余额

  dataSrc = PayLibrary.dataSrcOneCardPay; // 缴费类型:默认为一卡通充值

  // 虚拟键盘显示状态
  virtualKeyboardShow = false;

  constructor(private routerService: RouterService,
              private errorService: ErrorService,
              private route: ActivatedRoute,
              private userService: UserService) {
  }

  ngOnInit() {
    if (this.route.snapshot.paramMap.get('debt')) {
      this.debt = this.route.snapshot.paramMap.get('debt');
      this.totalAmount = this.debt;
      this.amountSting = this.debt;
      this.shortcutKey = '';
    } else {
      // 如果路由中有缴费类型的字段，则证明是住院缴费
      if (this.route.snapshot.paramMap.get('dataSrc')) {
        this.dataSrc = this.route.snapshot.paramMap.get('dataSrc');
        this.balanceAmount = this.userService.getInPatientInfo().remain_amt;
      } else {
        this.balanceAmount = this.userService.getOutPatientInfo().balance;
      }

      if (parseFloat(this.balanceAmount) < 0) {
        this.totalAmount = (-parseFloat(this.balanceAmount)).toString();
        this.shortcutKey = '';
      }
    }
  }

  // 快捷键输入金额
  shortcutKeyTap(keyNumber): void {
    this.totalAmount = keyNumber;
    this.shortcutKey = keyNumber;
    this.amountSting = '请输入缴费金额';
  }

  // 打开虚拟键盘
  openVirtualKeyboard(): void {
    this.virtualKeyboardShow = true;
  }

  goToPaymentTypeSelect(): void {
    if (parseFloat(this.totalAmount) === 0) {
      const infoMessage: InfoMessage = new InfoMessage(InfoLibrary.paymentAmountError);
      infoMessage.noRouter = true; // 关闭时无需跳转
      infoMessage.autoCloseTime = 3000; // 自动关闭倒计时毫秒
      this.errorService.showError(infoMessage);
    } else {
      const orderInfo = {totalAmount: this.totalAmount, dataSrc: this.dataSrc};
      // const orderInfo = {totalAmount: '0.01', dataSrc: this.dataSrc};
      this.routerService.goTo([PathLibrary.paymentTypeSelect, orderInfo]);
    }
  }
}



