// @see: ServerApiLibrary.unifiedOrder
export class OutputUnifiedOrder {
  appId: string; // appId
  timeStamp: string; // timeStamp
  nonceStr: string; // 随机字符串
  package: string; // package
  signType: string; // 签名类型
  paySign: string; // 签名
  patient_id: string; // 用户查询卡号
  real_name: string; // 姓名
  hospital_id: string; // 医院ID
  trade_no: string; // 订单号
  pay_url: string; // 支付URL
  totle_amt: string; // 总金额
  med_amount: string; // 医保支付金额
  discount_amt: string; // 优惠金额
  order_amount: string; // 自付金额
}



