import {ErrorHandler, NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {ServerRequestService} from 'app/protocol/server/server-request.service';
import {ShellRequestService} from 'app/protocol/shell/shell-request.service';

import {HttpService} from 'app/core/public/http.service';

import {AuthService} from 'app/core/services/auth.service';
import {BackService} from 'app/core/services/back.service';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';
import {InfoService} from 'app/core/services/info.service';
import {LogService} from 'app/core/services/log.service';
import {RouterService} from 'app/core/services/router.service';
import {SystemService} from 'app/core/services/system.service';
import {TimerService} from 'app/core/services/timer.service';
import {UserService} from 'app/core/services/user.service';

import {AppErrorHandler} from 'app/core/utility/app-error-handler';


@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [],
  providers: [
    ServerRequestService,
    ShellRequestService,
    AuthService,
    BackService,
    ErrorService,
    LoadingService,
    HttpService,
    InfoService,
    LogService,
    RouterService,
    SystemService,
    TimerService,
    UserService,
    {provide: ErrorHandler, useClass: AppErrorHandler}
  ],
})
export class CoreModule {
}



