// Shared feature module

import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {
  MatButtonModule,
  MatCheckboxModule,
  MatDialogModule,
  MatRadioModule,
  MatExpansionModule,
  MatChipsModule
} from '@angular/material';
import {NgxQRCodeModule} from '@techiediaries/ngx-qrcode';

import {StatusInfoComponent} from './status-info/status-info.component';
import {TimerComponent} from './timer/timer.component';
import {SystemAlertComponent} from './system-alert/system-alert.component';
import {MainHeaderComponent} from './main-header/main-header.component';
import {MainHeaderLogoComponent} from './main-header-logo/main-header-logo.component';
import {PaymentAmountSelectComponent} from './payment-amount-select/payment-amount-select.component';
import {VirtualKeyboardComponent} from './virtual-keyboard/virtual-keyboard.component';
import {PaymentTypeSelectComponent} from './payment-type-select/payment-type-select.component';
import {PaymentComponent} from './payment/payment.component';
import {PaymentResultComponent} from './payment-result/payment-result.component';
import {VirtualKeyboardFloatComponent} from './virtual-keyboard-float/virtual-keyboard-float.component';
import {ErrorDialogComponent} from './error-dialog/error-dialog.component';
import { LoadingComponent } from './loading/loading.component';
import { ErrorPageComponent } from './error-page/error-page.component';
import { ErrorWaitComponent } from './error-wait/error-wait.component';


@NgModule({
  imports: [
    CommonModule,
    MatButtonModule,
    MatCheckboxModule,
    MatDialogModule,
    MatRadioModule,
    MatExpansionModule,
    MatChipsModule,
    NgxQRCodeModule
  ],
  declarations: [
    StatusInfoComponent,
    TimerComponent,
    SystemAlertComponent,
    MainHeaderComponent,
    MainHeaderLogoComponent,
    PaymentAmountSelectComponent,
    VirtualKeyboardComponent,
    PaymentTypeSelectComponent,
    PaymentComponent,
    PaymentResultComponent,
    VirtualKeyboardFloatComponent,
    ErrorDialogComponent,
    LoadingComponent,
    ErrorPageComponent,
    ErrorWaitComponent,
  ],
  exports: [
    StatusInfoComponent,
    TimerComponent,
    SystemAlertComponent,
    MainHeaderComponent,
    MainHeaderLogoComponent,
    VirtualKeyboardComponent,
    ErrorDialogComponent,
    LoadingComponent,
    ErrorPageComponent,
  ],
  entryComponents: [
    SystemAlertComponent,
    ErrorDialogComponent
  ]
})
export class SharedModule {
}



