// @see: ShellApiLibrary.getConfig
export class OutputGetConfig {
  server: string; // 服务器地址
  hospitalID: string; // 医院ID
  deviceID: string; // 设备ID
  macAddress: string; // 设备mac地址
  download: string; // 版本更新地址
  webVersion: string; // web的当前版本号
  mode: string; // 运行模式
}



