// 封面上菜单项数据格式定义

export class CoverItem {
  itemType: number; //
  imageURL: string;
  title: string;
  path: string; // 路由地址

  constructor(itemType: number,
              imageURL: string,
              title: string,
              path: string) {
    this.itemType = itemType;
    this.imageURL = imageURL;
    this.title = title;
    this.path = path;
  }

}



