// 系统状态显示

import {Component, OnDestroy, OnInit} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';

import {SystemService} from 'app/core/services/system.service';
import {InfoService} from 'app/core/services/info.service';
import {InfoMessage} from 'app/core/objects/info-message';
import {DeviceParameter} from 'app/core/public/device-parameter';

@Component({
  selector: 'app-status-info',
  templateUrl: './status-info.component.html',
  styleUrls: ['./status-info.component.css']
})
export class StatusInfoComponent implements OnInit, OnDestroy {
  showInfoContent = DeviceParameter.isInfoDebugMode;

  infoArray: InfoMessage[] = []; // 状态信息
  maxInfoNum = 15;  // 状态信息最大显示条数

  private showInfoContentObservable: Observable<boolean>;
  private showInfoContentSubscription: Subscription;

  private infoObservable: Observable<InfoMessage>;
  private infoSubscription: Subscription;

  constructor(private systemService: SystemService) {
  }

  ngOnInit() {
    this.showInfoContentObservable = DeviceParameter.getInfoDebugMode();

    this.showInfoContentSubscription = this.showInfoContentObservable.subscribe((isInfoDebugMode: boolean) => {
      this.showInfoContent = isInfoDebugMode;
    });

    this.infoArray.push(new InfoMessage('--'));
    this.infoStart();
  }

  ngOnDestroy() {
    this.infoStop();
  }



  private infoStart(): void {

    this.infoObservable = InfoService.getInfo();
    this.infoSubscription = this.infoObservable.subscribe(
      (infoMessage: InfoMessage) => {
        if (this.infoArray.length >= this.maxInfoNum) {
          this.infoArray.pop();
        }
        this.infoArray.unshift(infoMessage);
        console.log('-- update! --', infoMessage.data)
      },
      error => {
        console.log('-- error! --')
      },
      () => {
        console.log('-- end! --')
      }
    );

    this.systemService.initUICompleted();

  }

  private infoStop(): void {
    if (this.infoSubscription) {
      this.infoSubscription.unsubscribe();
    }
  }

  clearLog(): void {
    this.infoArray.length = 1;
  }
}



