// 应用运行模式

export class ModeLibrary {
  public static readonly modeProduction: string = '0'; //  产品模式，设备上运行，不显示调试信息
  public static readonly modeDeviceDebug: string = '10'; //  设备调试模式，设备上运行，且显示调试信息
  public static readonly modeUserDebug: string = '50'; //  用户调试模式，设备上运行，使用读卡器但锁定为指定用户，且显示调试信息
  public static readonly modeShellWebDebug: string = '55'; //  用户调试模式，设备上运行，不使用读卡器，且显示调试信息
  public static readonly modeWebDebug: string = '99'; //  开发调试模式，脱壳运行，全模拟，不与壳交互
}



