// header, 显示 用户信息、返回按钮
import {Component, Input, OnInit} from '@angular/core';

import {UserService} from 'app/core/services/user.service';
import {SystemService} from 'app/core/services/system.service';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {PayLibrary} from 'app/core/libs/pay-library';

@Component({
  selector: 'app-main-header',
  templateUrl: './main-header.component.html',
  styleUrls: ['./main-header.component.css']
})
export class MainHeaderComponent implements OnInit {

  @Input() infoHidden: boolean;  // 是否显示倒计时
  @Input() timerShow: boolean;  // 是否显示倒计时
  @Input() timerMax: number; // 倒计时上限
  @Input() dataSrc: string;  // 缴费类型

  outpatientInfo: OutputQueryOutPatientInfo;
  inpatientInfo: OutputQueryInHospitalPatient;

  constructor(private userService: UserService,
              private systemService: SystemService) {
  }

  ngOnInit() {
    if (this.dataSrc === PayLibrary.dataSrcInpatient) {
      this.inpatientInfo = this.userService.getInPatientInfo();
    }else {
      this.outpatientInfo = this.userService.getOutPatientInfo();
    }
  }

  goToReturn(): void {
    this.systemService.backMainMenu();
  }

  // 定时器时间到
  timerCompleted(event: number) {
    this.systemService.backMainMenu();
  }
}



