// 住院病人信息展示页
import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

import {PathLibrary} from 'app/core/libs/path-library';
import {RouterService} from 'app/core/services/router.service';
import {PayLibrary} from 'app/core/libs/pay-library';

import {UserService} from 'app/core/services/user.service';
import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';

@Component({
  selector: 'app-inpatient-info',
  templateUrl: './inpatient-info.component.html',
  styleUrls: ['./inpatient-info.component.css']
})
export class InpatientInfoComponent implements OnInit {
  @Input() inpatientInfoShow: boolean;
  @Output() inpatientInfoShowChange = new EventEmitter<boolean>();

  inpatientInfo = new OutputQueryInHospitalPatient();

  constructor(private routerService: RouterService,
              private userService: UserService) {
  }

  ngOnInit() {
    this.inpatientInfo = this.userService.getInPatientInfo();
  }

  // 确认按钮点击
  confirmTap(): void {
    const dataSrcObj = {dataSrc: PayLibrary.dataSrcInpatient};
    this.routerService.goTo([PathLibrary.paymentAmountSelect, dataSrcObj]);
  }

  // 虚拟键盘取消按钮点击
  cancelTap(): void {
    this.inpatientInfoShow = false;
    this.inpatientInfoShowChange.emit(this.inpatientInfoShow);
  }
}



