// 封面 item 数据定义

import {CoverItem} from 'app/core/objects/cover-item';
import {PathLibrary} from 'app/core/libs/path-library';

export class CoverItemLibrary {

  public static readonly type1: number = 1; // 住院缴费
  public static readonly type2: number = 2; // 费用清单
  public static readonly type3: number = 3; // 门诊预约
  public static readonly type4: number = 4; // 健康护理
  public static readonly type5: number = 5; // 门诊缴费查询
  public static readonly type6: number = 6; // 住院缴费
  public static readonly type7: number = 7; // 一卡通充值

  public static itemData(): CoverItem[] {
    const coverData: CoverItem[] = [];
    coverData.push(new CoverItem(1, 'assets/images/icon_outpatient-payment.png', '门诊待缴费查询',
      PathLibrary.outpatientPaymentRecord));
    coverData.push(new CoverItem(2, 'assets/images/icon_hospital-pay-cost.png', '住院缴费',
      PathLibrary.inpatientLogin));
    coverData.push(new CoverItem(3, 'assets/images/icon_one-card-recharge.png', '一卡通充值',
      PathLibrary.paymentAmountSelect));
    // coverData.push(new CoverItem(4, 'assets/images/4.png',  '健康护理', PathLibrary.video));
    // coverData.push(new CoverItem(4, 'assets/images/4.png',  '健康护理', PathLibrary.login));
    return coverData;
  }

}



