// 封装了系统提示信息，主要用于调试时使用

import {Injectable} from '@angular/core';
import {Observable, Subscriber} from 'rxjs/Rx';

import {BaseService} from 'app/core/bases/base-service';
import {InfoMessage} from 'app/core/objects/info-message';


@Injectable()
export class InfoService extends BaseService {

  private static instance: InfoService; // 实现单例的理由是，便于其他类的调用（静态方法）

  private infoObservable: Observable<InfoMessage>; // 提供给外部的观察对象，系统信息
  private infoSubscriber: Subscriber<InfoMessage>;

  /**
   * 显示调试信息，供内部逻辑程序调用，一般只供 LogService 调用
   * @param {string} newInfo
   */
  public static showDebugInfo(newInfo: string, isError: boolean = false) {
    if (InfoService.instance) {
      InfoService.instance.updateDebugInfo(newInfo, isError);
    }
  }

  /**
   * 获取Observable，UI组件专用
   * @returns {Observable<InfoMessage>}
   */
  public static getInfo(): Observable<InfoMessage> {
    return InfoService.instance.infoObservable;
  }

  constructor() {
    super();
  }

  init() {
    if (!InfoService.instance) {
      InfoService.instance = this;

      this.infoObservable = new Observable(
        subscriber => {
          this.infoSubscriber = subscriber;
        }
      );
    }
  }

  // 更新调试信息
  private updateDebugInfo(newInfo: string, isError: boolean = false) {
    if (this.infoSubscriber != null) {
      const infoMessage: InfoMessage = new InfoMessage(newInfo);
      if (isError) {
        infoMessage.status = InfoMessage.statusError;
      }

      this.infoSubscriber.next(infoMessage);
    }
  }

}



