// 外壳接口定义，由工具生成

export class ShellApiLibrary {
  public static readonly statusSuc = '0000'; // 状态码，成功
  public static readonly statusErrCode = '4001'; // 状态码，指令编码错误
  public static readonly statusErrReadCard = '4002'; // 状态码，读卡错误

  public static readonly getConfig = '6001'; // 获取配置数据
  public static readonly log = '5010'; // 日志记录
  public static readonly readMagneticCard = '1001'; // 读磁条卡
  public static readonly readICCard = '1002'; // 读IC卡
  public static readonly readIDCard = '1001'; // 读身份证
  public static readonly readHealthCard = '1004'; // 读医保卡
  public static readonly shutDown = '5001'; // 关闭软件
  public static readonly updateVersion = '5002'; // 更新程序
  public static readonly setTimeForPowerOn = '1012'; // 设置自动开机时间
  public static readonly setTimeForPowerOff = '1014'; // 设置自动关机时间

  public static readonly test = 'test'; // 内容调试
}



