// 住院模块
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {SharedModule} from '../shared/shared.module';

import {MatButtonModule, MatCheckboxModule, MatDialogModule, MatRadioModule} from '@angular/material';
import {NgxQRCodeModule} from '@techiediaries/ngx-qrcode';
import {NgxBarcodeModule} from 'ngx-barcode';

import {InpatientLoginComponent} from './inpatient-login/inpatient-login.component';
import { InpatientInfoComponent } from './inpatient-info/inpatient-info.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    MatButtonModule,
    MatCheckboxModule,
    MatRadioModule,
    MatDialogModule,
    NgxQRCodeModule,
    NgxBarcodeModule
  ],
  declarations: [
    InpatientLoginComponent,
    InpatientInfoComponent
  ],
  entryComponents: []
})
export class InpatientModule {
}



