// @see: ServerApiLibrary.createOrder
export class OutputCreateOrder {
  id: string; // 订单主键ID
  biz_id: string; // 订单业务主键ID
  endorse_no: string; // 批单号
  trade_no: string; // 订单号
  data_src: string; // 数据来源
  customer_id: string; // 用户ID
  hospital_id: string; // 医院ID
  patient_id: string; // 就诊人ID
  patient_name: string; // 就诊人名称
  order_amount: string; // 订单金额
  order_status: string; // 订单状态
  fee_type: string; // 缴费类型
  pay_type: string; // 支付类型
  fee_date: string; // 缴费日期
  update_time: string; // 订单更新日期
  create_time: string; // 订单创建日期
  his_status: string; // his状态
  id_card: string; // 就诊人身份证号
  sign: string; // 签名字段
  p_hospital_id: string; // 卫计委ID
}



