// 日志记录，需要记录在日志文件或日志服务器中
// 日志级别：error, warn, info, debug
// 暂不实现具体逻辑

import {Injectable} from '@angular/core';

import {BaseService} from 'app/core/bases/base-service';
import {InfoService} from 'app/core/services/info.service';
import {ShellRequestService} from 'app/protocol/shell/shell-request.service';
import {DeviceParameter} from 'app/core/public/device-parameter';

@Injectable()
export class LogService extends BaseService {

  /**
   * 错误级别的日志, 该级别将被永久记录下来
   * @param {string} data 日志内容
   */
  public static error(data: string) {
    console.error('log ERROR', data); // 显示在 console 中
    InfoService.showDebugInfo(data, true); // 显示在调试面板中

    // 写入日志文件
    if (!DeviceParameter.isShellDebugMode) {
      ShellRequestService.logToShell('ERROR: ' + data); // 写入日志文件
    }
  }

  /**
   * 警告级别的日志
   * @param {string} data 日志内容
   */
  public static warn(data: string) {
    console.error('log WARN', data); // 显示在 console 中
    InfoService.showDebugInfo(data); // 显示在调试面板中

    // 写入日志文件
    if (!DeviceParameter.isShellDebugMode) {
      ShellRequestService.logToShell('WARN: ' + data); // 写入日志文件
    }
  }

  /**
   * 信息级别的日志
   * @param {string} data 日志内容
   */
  public static info(data: string) {
    console.log('log INFO', data); // 显示在 console 中
    InfoService.showDebugInfo(data); // 显示在调试面板中

    // 写入日志文件
    if (!DeviceParameter.isShellDebugMode) {
      ShellRequestService.logToShell('INFO: ' + data);
    }
  }

  /**
   * 调试级别的日志，一般都是用此级别，正式上线前应确保无使用此级别信息
   * @param {string} data 日志内容
   */
  public static debug(data: string) {
    console.log('log DEBUG', data); // 显示在 console 中
    InfoService.showDebugInfo(data); // 显示在调试面板中

    // 写入日志文件
    if (!DeviceParameter.isShellDebugMode) {
      ShellRequestService.logToShell('DEBUG: ' + data);
    }
  }

  constructor() {
    super();
  }

  init() {
  }

}



