// 对服务器 http 请求处理的封装，未来会提取到公共模块中

import {Injectable} from '@angular/core';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {Observable} from 'rxjs/Rx';

import {ResponseDataWrap} from 'app/core/objects/response-data-wrap';
import {LogService} from 'app/core/services/log.service';

@Injectable()
export class HttpService {

  private readonly timeoutMax = 18000; // 网络连接超时，毫秒

  constructor(private http: HttpClient) {
  }

  requestServer(api: string, parameter: object): Observable<ResponseDataWrap> {
    LogService.debug(
      'send http request. URL: ['
      + api
      + '], parameter:['
      + JSON.stringify(parameter)
      + ']');
    return this.http
      .post<ResponseDataWrap>(api, parameter)
      .timeout(this.timeoutMax)
      .map(this.extractData)
      .catch(this.handleError);
  }

  // 提取数据做额外处理
  private extractData(responseData: ResponseDataWrap) {
    console.log('----- 99 ------', responseData);
    LogService.debug(
      'get http response: ['
      + JSON.stringify(responseData)
      + ']');

    return responseData;
  }

  // HttpErrorResponse
  private handleError(error: HttpErrorResponse | any) {
    console.log('----- 100 ------', error);
    LogService.error(
      'get ERROR with http response: ['
      + JSON.stringify(error)
      + ']');

    let errMsg: string;
    if (error.message) {
      errMsg = error.message;
    } else {
      errMsg = error.toString();
    }
    return Observable.throw(errMsg);
  }

  // -------------------------------------

  // 暂时封存不用，待下一版本再优化
  requestRemoteJson(url: string): Observable<ResponseDataWrap> {
    console.log('----- 10 ------');
    LogService.debug(
      'send http request. URL: ['
      + url
      + ']');
    // return this.http
    //   .jsonp(url, 'john')
    //   .timeout(this.timeoutMax)
    //   .map(this.extractData)
    //   .do(console.log)
    //   .catch(this.handleError);
    return this.http
      .get<ResponseDataWrap>(url)
      .timeout(this.timeoutMax)
      .map(this.extractData)
      .catch(this.handleError);
  }

}



